.profile {
  margin-top: 7px;
  width: 100%;
  border-radius: 16px;
  background-color: $white;
  min-height: 40vh;
  &__post_image {
    width: 600px;
    height: auto;
    margin: auto;

    @media screen and (max-width: 500px) {
      width: 380px;
    }
  }

  &__post_video {
    width: 600px;
    height: 700px;
    background-color: black !important;
    @media screen and (max-width: 500px) {
      height: auto;
      width: 380px;
    }
  }

  &__header {
    background-repeat: no-repeat;
    border-radius: 8px 8px 0px 0px;
    background-size: 100% 210px;
    display: flex;
    flex-direction: column;
    align-items: center;

    img.profile_photo {
      width: 160px;
      background-color: $white;
      height: 160px;
      border: 4px solid $white;
      border-radius: 100%;
      margin-top: 70px;
    }
    img.sub_icon {
      height: 14px;
      width: 14px;
      margin-top: -9px;
    }
  }

  &__name {
    font-family: Factor A;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
    color: $black;
    margin-top: 10px;
  }

  &__nickname {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.6);
  }

  &__info {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    color: #000000;
  }

  &__status {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;
    margin-left: 24px;
  }

  &__statusFull {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    color: #fb5734;
    margin-left: 24px;
  }

  &__post {
    margin-top: 20px;
  }

  &__noPost {
    background: linear-gradient(135deg, #fa5e5b 10%, #cfe4ed 100%);
    width: '100%';
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    margin: 12px;
  }

  &__postInfo {
    display: flex;
    flex-direction: column;
    padding: 0px 24px;
  }

  &__postUserInfo {
    img {
      width: 50px;
      height: 50px;
      border-radius: 100%;
    }

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__postAgo {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 21px;
    color: rgba(0, 0, 0, 0.6);
    margin-right: 16px;
  }

  &__postText {
    font-family: Factor A;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    margin-top: 10px;
    line-height: 21px;
    color: #000000;
  }

  &__postMain {
    border-bottom: 1px solid $grey;
  }

  &__postIMG {
    display: flex;
    justify-content: center;
  }

  &__headerButtons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 24px;
    padding-left: 32px;
    padding-right: 32px;
  }
}